.a1{margin: 0;text-decoration: none;padding: 0;}
.main-menu {

background:#212121;
opacity: 90%;
border-right:0.07142857142857142vw solid #e5e5e5;
position:fixed;
/* top:20vw; */
bottom:10vw;
height:7%;
right:0;
/* top: 0%; */
width:4.285714285714286vw;
overflow:hidden;
-webkit-transition:width .05s linear;
transition:width .05s linear;
-webkit-transform:translateZ(0) scale(1,1);
z-index:1000;
margin-top: 10vw;
}

.main-menu>ul {
margin:0.5vw 0;


}



.main-menu li {
position:relative;
display:block;
width:17.857142857142858vw
}



.main-menu li>a {
position:relative;
display:table;
border-collapse:collapse;
border-spacing:0;
color:#999;
 font-family: arial;
font-size: 1vw;
text-decoration:none;
-webkit-transform:translateZ(0) scale(1,1);
-webkit-transition:all .1s linear;
transition:all .1s linear;
}

.main-menu .nav-icon {
position:relative;
display:table-cell;
width:4.285714285714286vw;
height:2.571428571428571vw;
text-align:center;
vertical-align:middle;
font-size:1.2857142857142856vw;
}

.main-menu .nav-text {
position:relative;
display:table-cell;
vertical-align:middle;
width:13.571428571428571vw;
  font-family: 'Titillium Web', sans-serif;
}

.main-menu>ul.logout {
position:absolute;
left:0;
bottom:0;
}

.no-touch .scrollable.hover {
overflow-y:hidden;
}

.no-touch .scrollable.hover:hover {
overflow-y:auto;
overflow:visible;
}

a:hover,a:focus {
text-decoration:none;
}

nav {
-webkit-user-select:none;
-moz-user-select:none;
-ms-user-select:none;
-o-user-select:none;
user-select:none;
}

nav ul,nav li {
outline:0;
margin:0;
padding:0;
}
.main-menu li:hover>a,nav.main-menu li.active>a,.dropdown-menu>li>a:hover,.dropdown-menu>li>a:focus,.dropdown-menu>.active>a,.dropdown-menu>.active>a:hover,.dropdown-menu>.active>a:focus,.no-touch .dashboard-page nav.dashboard-menu ul li:hover a,.dashboard-page nav.dashboard-menu ul li.active a {
color:#fff;
background-color:#000000;
